import { Link, useNavigate } from "react-router-dom"
import "./../../css/PrivacyPolicy.css"
import { Box, Breadcrumbs, Typography } from "@mui/material";
import { Container } from "react-bootstrap";
function PrivacyPolicy() {
    const navigate = useNavigate()
    return (
        <>
            <main>
                <Box
                    sx={{
                        position: 'relative',
                        backgroundImage: 'url(/assets/img/breadcurmb/termsandconditions.jpg)',
                        backgroundSize: 'cover',
                        backgroundPosition: 'center',
                        py: { xs: 6, md: 10 },
                        color: 'white',
                        zIndex: 1,
                        '&::after': {
                            content: '""',
                            position: 'absolute',
                            top: 0,
                            left: 0,
                            right: 0,
                            bottom: 0,
                            backgroundColor: 'rgba(0,0,0,0.5)',
                            zIndex: -1,
                        },
                    }}
                >
                    <Container maxWidth="md" sx={{ textAlign: 'center' }}>
                        <Typography variant="h3" fontWeight="bold" gutterBottom>
                            Privacy Policy
                        </Typography>

                        <Breadcrumbs
                            
                            aria-label="breadcrumb"
                            sx={{ justifyContent: 'center', display: 'flex', color: 'white' }}
                        >
                            <Link underline="hover" color="inherit" href="/">
                                Home
                            </Link>
                            <Typography color="white"> Privacy Policy</Typography>
                        </Breadcrumbs>
                    </Container>
                </Box>
                <div className="tr-about-area z-index-1 pt-120 pb-105 ptr container" style={
                    {
                        color: "black"
                    }
                }>
                    <div class="container">
                        <div class="mb-4">
                            <p>We, <strong>Tech Money Private Limited</strong>, hereby referred to as
                                <strong>Bank Mudra</strong>, appreciate your trust and protect your privacy. Our registered office is
                                at First Floor, Ramji Market, Near Indian Petrol Pump, Sector 61, Gurgaon, Haryana-122101. The
                                methods by which we gather, store, and use your data are described in this Privacy Policy.
                                Kindly carefully read this privacy statement. You willingly consent to our use and dissemination
                                of your personal information in line with this Privacy Policy by downloading and using the Bank Mudra
                                application, website, or WAP site. Please do not use or access Bank Mudra if you disagree with the
                                terms of this policy.</p>
                        </div>
                        <h4>Notice</h4>
                        <p>Without giving notice, our privacy policy is subject to change at any time. Please periodically
                            review the policy to make sure you are informed of any changes. This privacy statement is consistent
                            across desktop and mobile WAP sites, as well as Bank Mudra applications.</p>

                        <h4>General</h4>
                        <p>Your email address or cell phone number will not be used for unsolicited emails or SMS, nor will we
                            sell, distribute, or rent your personal information to any third party. Bank Mudra will only send emails
                            and/or SMS in relation to delivering the services and goods that have been agreed upon, as detailed
                            in this privacy policy. We may occasionally make public broad statistical data about Bank Mudra and its
                            users, including visits, the quantity and kind of goods and services bought, etc. We maintain the
                            right to share your personal information with any third party that requests access to it in a way
                            that complies with the law.</p>

                        <h4>Personal Information</h4>
                        <p>All information that can be used to identify or link a specific person is considered personal
                            information. Examples of this type of information include name, address, mailing address, phone
                            number, email address, credit card number, cardholder name, card expiration date, information
                            about your mobile phone, DTH service, data card, electricity connection, Smart Tags, and any
                            information that you voluntarily provide in connection with using any of NeedU’s services. We may
                            obtain information about your mobile or tab device, the domain and host from which you access the
                            internet, the computer’s IP address or Internet service provider (ISP) you are using, and anonymous
                            site statistics when you browse Bank Mudra.</p>

                        <h4>Use of Personal Information</h4>
                        <ol>
                            <li>We use personal information to provide you with services and products you explicitly
                                requested, resolve disputes, troubleshoot concerns, help promote safe services, collect
                                money, measure consumer interest in our services, inform you about offers, products,
                                services, updates, customize your experience, detect and protect us against error, fraud, and
                                other criminal activity, enforce our terms and conditions, etc.</li>
                            <li>We also use your contact information to send you offers based on your previous orders and
                                interests. We may occasionally ask you to complete optional online surveys. These surveys
                                may ask you for contact information and demographic information (like zip code, age,
                                gender, etc.). We use this data to customize your experience at Bank Mudra, providing you with
                                content that we think you might be interested in and to display content according to your
                                preferences.</li>
                            <li>We may share Personal Information as well as non-personal information of the User with
                                the courts, police authorities, or any other government/regulatory/statutory authority, in
                                case these are required by them in relation to any proceedings pending before them.
                                Cookies</li>
                        </ol>

                        <h4>Cookies</h4>
                        <ol>
                            <li>A “cookie” is a small piece of information stored by a web server on a web browser so it can be
                                later read back from that browser. Bank Mudra uses cookie and tracking technology depending on the
                                features offered. No personal information will be collected via cookies and other tracking
                                technology; however, if you previously provided personally identifiable information, cookies may
                                be tied to such information. Aggregate cookie and tracking information may be shared with third
                                parties.</li>

                            <li>A cookie is a small file that asks permission to be placed on the User’s desktop/laptop/mobile
                                device’s memory drive. Once the User agrees, the file is added, and the cookie helps analyse web
                                traffic. Cookies allow Services to respond to the User as an individual. The Services can tailor
                                its operations to the User’s needs, likes, and dislikes by gathering and remembering information
                                about the User’s preferences.</li>

                            <li>Cookies help us better understand User’s behaviour, and tell us which parts of the Services
                                Users have visited, and facilitate and measure the effectiveness of products and services
                                offered by us. We treat information collected by cookies and other technologies as non-personal
                                information. However, to the extent that internet protocol (IP) addresses or similar identifiers
                                are considered Personal Information by local law, we also treat these identifiers as Personal
                                Information.</li>

                            <li>We use traffic log cookies to identify which pages are being used. This helps us analyse data
                                about web page traffic and improve our Services in order to tailor it to customer needs. We only
                                use this information for statistical analysis purposes and then the data is removed from the
                                system.</li>

                            <li>Overall, cookies help us provide Users with a better experience of the Services, by enabling us
                                to monitor which pages Users find useful and which the Users do not. A cookie in no way gives us
                                access to the User’s desktop/laptop/mobile device or any information about the User, other than
                                the data a User chooses to share with us.</li>

                            <li>A User can choose to accept or decline cookies. Most web browsers automatically accept cookies,
                                but a User can usually modify its browser setting to decline cookies if the user prefers. This
                                may prevent the User from taking full advantage of the Services.</li>

                        </ol>

                        <h4>Links to Other Sites</h4>
                        <p>Our site links to other websites that may collect personally identifiable information about you.
                            Bank Mudra is not responsible for the privacy practices or the content of those linked websites.</p>

                        <h4>Security</h4>
                        <ol>
                            <li>Bank Mudra has stringent security measures in place to protect the loss, misuse, and alteration of
                                the information under our control. Whenever you change or access your account information, we
                                offer the use of a secure server. Once your information is in our possession, we adhere to
                                strict security guidelines, protecting it against unauthorized access.</li>

                            <li>We are committed to ensuring that the User’s Personal Information is secure. We adopt
                                appropriate data collection, storage, and processing practices and security measures to protect
                                against unauthorized access, alteration, disclosure, or destruction of the User’s Personal
                                Information, username, password, transaction information, and data stored in our Services.</li>

                            <li>When a User uses services and products offered by us through the Services, the Personal
                                Information shared by the User is visible to the other users and can be read, collected, or used
                                by them. The User is responsible for the Personal Information User chooses to submit in these
                                instances. For example, if the User lists his/her name and email address in a blog/forum
                                posting, that information is public. Users are requested to take care when using these features.
                            </li>

                        </ol>

                        <h4>Consent</h4>
                        <p>By using Bank Mudra and/or by providing your information, you consent to the collection and use
                            of the information you disclose on Bank Mudra in accordance with this Privacy Policy, including
                            but not limited to your consent for sharing your information as per this privacy policy.</p>

                        <h4>What we collect?</h4>
                        <p>We may collect and/or gain access to and/or record certain information including personal information
                            from or of User in a variety of ways, including, but not limited to, when User accesses and uses the
                            Services and in connection with other activities, services, features, or resources we make available
                            on the Services.</p>

                        <p>These information may relate to and include but not limited to:</p>
                        <ul class="list-group mb-4">
                            <li class="list-group-item">Your name</li>
                            <li class="list-group-item">Email address</li>
                            <li class="list-group-item">Address</li>
                            <li class="list-group-item">Phone number</li>
                            <li class="list-group-item">Your unique device ID (persistent/non-persistent)</li>
                            <li class="list-group-item">Hardware type</li>
                            <li class="list-group-item">International mobile equipment identity (“IMEI”)</li>
                            <li class="list-group-item">Version of your operating system (“OS”)</li>
                            <li class="list-group-item">Your device name</li>
                            <li class="list-group-item">Your email address (if connected to Facebook or Google+)</li>
                            <li class="list-group-item">Your location (based on your Internet Protocol (“IP”) address)</li>
                            <li class="list-group-item">Contact list (on your mobile phone)</li>
                            <li class="list-group-item">Applications installed by User through our mobile application</li>
                            <li class="list-group-item">Applications uninstalled by User</li>
                            <li class="list-group-item">Foreground running apps/process (RUN event)</li>
                            <li class="list-group-item">Read SMS</li>
                            <li class="list-group-item">Network information</li>
                            <li class="list-group-item">User behaviour analysis</li>
                            <li class="list-group-item">Demographic information (such as preferences and interests etc.)</li>
                            <li class="list-group-item">Credit/Debit Card information (Note: We do not store credit/debit card
                                information on our servers)</li>
                        </ul>

                        <h4>Purpose of Collection</h4>
                        <ul class="list-group mb-4">
                            <li class="list-group-item">Administer the Services.</li>
                            <li class="list-group-item">Personalize the Services for User.</li>
                            <li class="list-group-item">End-to-User direct links to the Services.</li>
                            <li class="list-group-item">Process transactions.</li>
                            <li class="list-group-item">Process installation.</li>
                            <li class="list-group-item">Send User our newsletter.</li>
                            <li class="list-group-item">Develop, deliver, and improve our products, services, content, and
                                advertising.</li>
                            <li class="list-group-item">Auditing, data analysis, and research to improve the Services.</li>
                            <li class="list-group-item">Troubleshooting and helping us to understand usage trends.</li>
                            <li class="list-group-item">Send alerts to User.</li>
                            <li class="list-group-item">Marketing and promotion of the Services.</li>
                        </ul>
                        <h4>Collection and Use of Non-Personal Information</h4>
                        <ul class="list-group mb-4">
                            <li class="list-group-item">We may collect non-personal information about Users whenever they use
                                and interact with the Services. Non-personal information may include the browser name, the type
                                of computer, and technical information about means used by the User to connect to our Services,
                                such as information about the operating system and the internet service providers utilized, and
                                other similar information.</li>
                            <li class="list-group-item">This information is aggregated and used to help us provide more useful
                                information to the User and to understand which parts of the Services, its products, and
                                services are of most interest.</li>
                        </ul>

                        <p><strong>Note:</strong> If we do combine non-personal information with Personal Information, the
                            combined information will be treated as Personal Information for as long as it remains combined.</p>

                    </div>
                </div>
                <div className="locationdiv">
                    <div className="tr-inner-contact-map">
                        <iframe
                            src="https://www.google.com/maps/embed?pb=..."
                            allowFullScreen=""
                            loading="lazy"
                            referrerPolicy="no-referrer-when-downgrade"
                            title="Google Map"
                        ></iframe>
                    </div>
                    <div className="container-fluid mt-5 bgcolor2">
                        <div className="container py-5">
                            <div className="row">
                                <div className="col-lg-6 wow fadeInLeftBig animated" style={{ visibility: "visible", animationDuration: "2s" }}>
                                    <h2 className="fs-1 fw-bolder">Want to Chat? Feel free to Contact our Team.</h2>
                                    <p className="fs-4 text-white">If you have anything in mind just contact us with our expert.</p>
                                </div>
                                <div
                                    className="col-lg-6 txtRight wow rotateIn animated"
                                    onClick={() => navigate("/contact")}
                                    style={{ visibility: "visible", animationDuration: "2s", cursor: "pointer" }}
                                >
                                    <div className="circleimg">
                                        <img src="assets/img/svg/icon_30.svg" alt="Contact Icon" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>

        </>
    )
}
export default PrivacyPolicy;